const ImageWithFallback = ({ src, alt, ...props }) => {
  const handleError = (e) => {
    console.error(`: ${src}`);
    e.target.src = `${process.env.PUBLIC_URL}/images/teapot-silhouette.png`; // 
  };

  return (
    <img
      src={src}
      alt={alt}
      onError={handleError}
      {...props}
    />
  );
};

